body{
  background-color: blueviolet;
}

.App{
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input{
  background-color: darkblue;
  border: 0;
  width: 250px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
}

#to-do-form button{
  height: 50px;
  width: 60px;
  border-radius: 5px;
  border: 0;
  background-color: yellowgreen;
  cursor: pointer;
  font-weight: bold;
  color: white;
}